body {
    margin: 0;
    font-family: "MyriadArabic";
    font-size: 16px;
}

.header {
    background-image: url(../img/header.jpg);
    padding: 30px 0 100px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    @media(max-width:767px) {
        padding: 10px 0 0;
        margin-top: -20px;
    }

    .header__title {
        font-family: "MyriadArabic";
        text-align: center;
    }

    .header__desc {
        font-family: "MyriadArabic";
        text-align: center;
        margin-top: 10px;
        font-size: 16px;
    }

    .header__tab {
        background-color: rgba(0, 0, 0, .5);
        margin-top: 80px;
        border-radius: 4px;
        padding: 10px;

        @media(max-width:767px) {
            border-radius: 0px;
            margin-top: 30px;
        }
    }
}

.menu {
    padding: 10px 0;
    border-bottom: 1px solid #e1e1e1;
    z-index: 1000;
    position: relative;
    background-color: #fff;
    box-shadow: 0px 0px 10px #ccc;

    img {
        max-height: 50px;
    }
}

.services {
    padding: 40px 0;

    .services__item {
        text-align: center;

        @media(max-width:991px) {
            margin-bottom: 15px;
        }

        img {
            margin: auto;
        }

        .services__item__title {
            font-weight: bold;
            font-size: 16px;
            color: #222;
            margin-top: 10px;
        }

        .services__item__desc {
            font-weight: 500;
            font-size: 16px;
            color: #555;
            line-height: 20px;
            margin-top: 10px;
        }
    }
}

.box {
    box-shadow: 0px 0px 4px #999;
    padding: 50px 0;
    margin: 50px 0;

    .box__title {
        text-align: center;
        margin-bottom: 20px;
        font-weight: 600;
        color: #0fc239 !important;
        font-size: 20px;
        font-family: "MyriadArabic";
    }

    .box__desc {
        color: #222;
        font-size: 16px;
        line-height: 30px;
        text-align: justify;
        font-weight: 500;

        @media (max-width:767px) {
            padding: 0px 10px;
        }
    }
}

.customer {
    padding: 10px 0;
    text-align: center;

    .customer__title {
        text-align: center;
        margin-bottom: 0px;
        font-weight: 600;
        color: #0fc239 !important;
        font-size: 20px;
        font-family: "MyriadArabic";
    }

    .customer__img {
        @media(max-width:500px) {
            margin-top: 20px;
        }
    }
}

.footer {
    background-color: #555;
    padding: 20px 0;
    color: #fff;
    text-align: center;
    font-family: "MyriadArabic";

    a {
        color: #0fc239 !important
    }
}

.navbar-inverse {
    background-color: transparent;
    border-color: transparent;
}

.navbar-inverse .navbar-nav>.active>a,
.navbar-inverse .navbar-nav>.active>a:focus,
.navbar-inverse .navbar-nav>.active>a:hover {
    background-color: transparent;
    color: #0fc239 !important;
}

.navbar-inverse .navbar-nav>li>a:focus,
.navbar-inverse .navbar-nav>li>a:hover {
    background-color: transparent;
    color: #0fc239 !important;
}

.navbar {
    margin-bottom: 0px;
}

.navbar-brand {
    padding: 0;
}

.navbar-inverse .navbar-toggle .icon-bar {
    background-color: #000;
}

.navbar-inverse .navbar-toggle:focus,
.navbar-inverse .navbar-toggle:hover {
    background-color: transparent;
}

.navbar-inverse .navbar-collapse,
.navbar-inverse .navbar-form {
    border-color: #10d83f;
}

.tab-content {
    background-color: #fff;
    padding: 25px 10px 10px;
}

.nav-tabs>li>a {
    color: #fff;
    font-size: 20px;
}

.form-group {
    position: relative;
}

.form-group>i {
    position: absolute;
    top: 8px;
    left: 8px;
    color: #969393;
    z-index: 1;
}

.nav-tabs>li>a:hover {
    background-color: transparent;
    border-color: transparent;
}

.margin-bottom-10 {
    margin-bottom: 10px;
}


label {
    position: relative;
    cursor: pointer;
    color: #666;
    font-size: 30px;
}

input[type="checkbox"],
input[type="radio"] {
    position: absolute;
    visibility: hidden;
}

/*Check box*/
input[type="checkbox"]+.label-text:before {
    content: "\f096";
    font-family: "FontAwesome";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    width: 1em;
    display: inline-block;
    margin-right: 5px;
    position: relative;
    top: 4px;
}

.form-check label {
    position: relative;
}

.form-check label .price {
    color: #999;
    position: absolute;
    left: 0;
    font-size: 14px;
}

input[type="checkbox"]:checked+.label-text:before {
    content: "\f14a";
    color: #2980b9;
    animation: effect 250ms ease-in;
}

input[type="checkbox"]:disabled+.label-text {
    color: #aaa;
}

input[type="checkbox"]:disabled+.label-text:before {
    content: "\f0c8";
    color: #ccc;
}

/*Radio box*/

input[type="radio"]+.label-text:before {
    content: "\f10c";
    font-family: "FontAwesome";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    width: 1em;
    display: inline-block;
    margin-right: 5px;
}

input[type="radio"]:checked+.label-text:before {
    content: "\f192";
    color: #61ad44;
    animation: effect 250ms ease-in;
}

input[type="radio"]:disabled+.label-text {
    color: #aaa;
}

input[type="radio"]:disabled+.label-text:before {
    content: "\f111";
    color: #ccc;
}

/*Radio Toggle*/

.toggle input[type="radio"]+.label-text:before {
    content: "\f204";
    font-family: "FontAwesome";
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    width: 1em;
    display: inline-block;
    margin-right: 10px;
}

.toggle input[type="radio"]:checked+.label-text:before {
    content: "\f205";
    color: #16a085;
    animation: effect 250ms ease-in;
}

.toggle input[type="radio"]:disabled+.label-text {
    color: #aaa;
}

.toggle input[type="radio"]:disabled+.label-text:before {
    content: "\f204";
    color: #ccc;
}


@keyframes effect {
    0% {
        transform: scale(0);
    }

    25% {
        transform: scale(1.3);
    }

    75% {
        transform: scale(1.4);
    }

    100% {
        transform: scale(1);
    }
}

label {
    font-size: 16px;
}

.form-control:focus {
    border-color: #21ce21;
    box-shadow: inset 0 1px 1px #21ce21, 0 0 1px #21ce21;
}

.tooltip-inner {
    font-family: "MyriadArabic";
}

.btn {
    outline: none !important;
}

.width-100 {
    width: 100%;
}

.weight-600 {
    font-weight: 600;
}

input[type="checkbox"]:checked+.label-text:before {
    color: green;
}

.dropdown-menu {
    padding: 10px;
    width: 200px;

    @media(max-width:767px) {
        width:100%;
    }

    .dropdown-menu__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 5px 0;

        .dropdown-menu__item__title,
        .dropdown-menu__item__price {
            font-size: 16px;
            font-weight: 600;
        }

        .dropdown-menu__item__btn {
            color: #999;
            font-size: 10px;
            margin: 0 2px;

            i {
                position: relative;
                top: 1px;
            }
        }
    }
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
}

.dropdown-menu>li>a {
    font-weight: 600;
    font-size: 16px;
    padding: 3px 0px;
    color: #919191;

    i {
        color: #919191;
        position: relative;
        top: 4px;
        margin-left: 6px;
    }
}

.dropdown-menu>li>a:focus, .dropdown-menu>li>a:hover {
    background-color: #5cb85c;
}

.padding-0 {
    padding: 0px !important;
}

.padding-10 {
    padding: 10px !important;
}


.datepicker-plot-area  {
    font-family: "MyriadArabic";
}

.datepicker-plot-area .header {
    background-image: none;
    padding-top: 0px;
}

.datepicker-plot-area .datepicker-day-view .table-days td.today span {
    font-family: "MyriadArabic";
    font-size: 16px;
}

.datepicker-plot-area .datepicker-day-view .table-days td span {
    font-family: "MyriadArabic";
    font-size: 16px;
}

.header-row-cell , .toolbox , .datepicker-navigator {
    font-family: "MyriadArabic";
}

.pwt-btn-today , .pwt-btn-calendar , .datepicker-navigator {
    font-size: 16px !important;
}

.navbar-inverse .navbar-nav>.open>a, .navbar-inverse .navbar-nav>.open>a:focus, .navbar-inverse .navbar-nav>.open>a:hover {
    background-color: transparent;
    color: #9d9d9d;
}

.navbar-inverse .navbar-nav .open .dropdown-menu>li>a:focus, .navbar-inverse .navbar-nav .open .dropdown-menu>li>a:hover {
    color: #9d9d9d;
}

.text-success {
    color: green;
}